body{
    background-color: #3a3b3c;
    color: white;
    text-align: center;
    font-size: 20px;
    font-family: 'Ubuntu';
    padding-top: 5%;
    word-wrap: break-word;
}

.logo{
    width: 250px;
    height: 250px;
}

.App{
    display: flex;
    flex-direction: column;
    align-items: center;
}

.WalletInfo{
    width: 500px;
}

.Block{
    border: 1px solid white;
    padding: 5%;
    margin: 2%;
}

.Transaction{
    padding: 5%;
}

.ConductTransaction, .TransactionPool {
    margin: 10%;
}

.ValidationToggle {
    margin-left: 1250px;
    margin-right: auto;
    margin-top: -50px;
}

a{
    color: #e66;
    text-decoration: underline;
}

a:hover{
    color: white;
    text-decoration: underline;
}